import React, {useState} from 'react'
import { Button, Card, CardBody, Col, Container, Row } from 'reactstrap'
import {baseUrl} from '../../shared/baseUrl'
import * as apiConstant from '../../shared/apiConstant'
import {MDBDataTableV5} from 'mdbreact'
import { Link } from 'react-router-dom'

export default function GetList() {

    const [data, setData] = useState({
        columns: [
            {
                label: 'Komoditas',
                field: 'komoditas',
                sort: 'asc',
                width: 130
              },
              {
                label: 'Area Provinsi',
                field: 'area_provinsi',
                sort: 'asc',
                width: 100
              },
              {
                label: 'Area Kota',
                field: 'area_kota',
                sort: 'asc',
                width: 100
              },
              {
                label: 'Size',
                field: 'size',
                sort: 'asc',
                width: 100
              },
              {
                label: 'Price',
                field: 'price',
                sort: 'asc',
                width: 100
              },
        ],
        rows: [

        ]
    })

    React.useEffect( () => {
        fetchData()
    },[])

    const fetchData = () => {
        fetch(baseUrl+apiConstant.LIST)
        .then( (response) => {
            return response.json()
        })
        .then( (datax) => {
            let temp = datax.filter(({uuid,komoditas}) => {
                return uuid != null && komoditas != null
            })
            setData({...data,rows: temp})
        })
    }

    return(
        <div>
            <Container>
                    <h3 className='mt-5'>List Harga Komoditas Perikanan</h3>
                    <Card className='my-4'>
                        <CardBody>
                        <Row>
                            <Col md='12' xs='12'>
                            <Link to='/insert-perikanan'>
                            <Button className='float-right' color="primary"><em className="fa fa-plus pr-3"></em>Add Data</Button>
                            </Link>
                            </Col>
                            <Col md='12' xs='12'>
                            <MDBDataTableV5 striped responsive hover={true} scrollX data={data}/>
                            </Col>
                        </Row>
                        </CardBody>
                    </Card>
            </Container>
        </div>
    )
}